
.root{
  position:relative;
}
#editor{
  position: absolute;
  top: 30px;
  right: 0;
  bottom: 0;
  left: 0;
  height:400px;
  border:1px solid #eee;
  border-left:0;

  color:#616161;
  font-size: 12px;
}
#editor .ace_gutter{
  background-color: #eee;
}

.title{
  border:1px solid #eee;
  background-color: #fafafa;
  height:30px;
  font-size: 13px;
  padding:7px;
  color:#757575
}



